/* css reset */
*{box-sizing: border-box;}
*:before, *:after{box-sizing: border-box;}
*{margin: 0;padding: 0;}
ul,ol{list-style: none;}
a{color: inherit;text-decoration: none;}
img{
    max-width: 100%;
    max-height: 100%;
}

.icon {
    width: 1em; height: 1em;
    vertical-align: -0.15em;
    fill: currentColor;
    overflow: hidden;
 }

/* style  */
body{
    background: #eee;
font-family: Arial;
}

.globalHeader{
    margin: 20px;
}

@media (min-width:500px){
    .globalHeader{
        margin: 60px 0  100px;
    }
}
.searchForm{
    display: flex; 
    justify-content:space-between;
}

@media (min-width:500px){
    .searchForm{
        max-width: 400px;
        margin-left: auto;
        margin-right: auto;
    } 
}

.searchForm > input{
    width: 100%;/*会把搜索挤换行*/
    margin-right: 10px;
    height: 40px;
    padding: 0px 10px;
    border: 1px solid #ddd;
    border-radius: 4px;
}
.searchForm > button{
    white-space: nowrap;/*搜索横向显示*/
    padding: 0 28px;
    border: 1px solid #ddd;
    border-radius: 4px;
    background: #0282b9;
    color: white;
    font-size: 16px;
}

.globalMain{
    max-width: 900px;
    margin-left: auto;
    margin-right: auto;
}

.siteList{
    margin: 20px;
    display: flex;
    flex-wrap: wrap;
    justify-content:space-between;
}

@media(min-width:500px){
    .siteList{
        margin-left: 0;
        margin-right:-25px;
        justify-content: flex-start;
    }
}

.siteList > li{
margin-bottom: 10px;
}
@media(min-width:500px){
    .siteList > li{
        margin-right: 25px;
        }
}
 
.siteList .site{
    position: relative;
    border:1px solid #ddd;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    background: white;
    margin-bottom: 10px;
    border-radius: 4px;
    padding: 20px 0;
    cursor: pointer;
}

.siteList .site >.logo{
    width: 64px;
    height: 64px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-size: 64px;
}
.siteList .site >.link{
    font-size: 14px;
    margin-top: 4px;
}

.siteList .site >.close{
    position: absolute;
    top: 1px;
    right: 5px;
    color: rgb(202, 202, 209);
    display: none;
}

.siteList .site:hover >.close{
display: block;
}


.siteList .addButton{
    border: 1px solid #ddd;
    background: white;
    width: 160px;
    display: flex;
    justify-content: center;
    align-items: center;
    flex-direction: column;
    padding: 20px 0;
}

.siteList .addButton .icon{
    width: 56px;
    height: 56px;
}
.siteList .addButton .text{
font-size: 14px;
margin-top: 4px;
}

.siteList .addButton .icon-wrapper{
   width: 64px;
   height: 64px;
   display: flex;
   justify-content: center;
   align-items: center;
}